@import url("https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap");

//Global
.container {
  width: 80vw;
  margin: 5vh auto 10vh;
}

p {
  font-size: 18px;
  margin-bottom: 10px;
  line-height: 32px;
}

.section {
  margin: 15vh auto 0;
}

.section-header {
  font-family: "Times New Roman", Times, serif;
  font-size: 3rem;
  color: #000;
}

//--Animations--------------------------------------------------
.animatedFadeIn {
  animation-fill-mode: both;
  animation-duration: 1000ms;
  animation-delay: 0ms;
  animation-iteration-count: 1;
  opacity: 0;
  animation-name: fadeInUp;
}

.fadeInDelay1 {
  animation-duration: 1260ms;
}

.fadeInDelay2 {
  animation-duration: 1600ms;
}

.fadeInDelay3 {
  animation-duration: 1987ms;
}

@keyframes fadeInUp {
  from {
    transform: translate3d(0, 40px, 0);
  }

  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

/* Hover Sweep Right */
.hvr-sweep-to-right {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
  -webkit-transition-property: color;
  transition-property: color;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
}
.hvr-sweep-to-right:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: white;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transform-origin: 0 50%;
  transform-origin: 0 50%;
  -webkit-transition-property: transform;
  transition-property: transform;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
}
.hvr-sweep-to-right:hover, .hvr-sweep-to-right:focus, .hvr-sweep-to-right:active {
  color: black;
}
.hvr-sweep-to-right:hover:before, .hvr-sweep-to-right:focus:before, .hvr-sweep-to-right:active:before {
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}

/* Bob */
@-webkit-keyframes hvr-bob {
  0% {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px);
  }
  50% {
    -webkit-transform: translateY(-4px);
    transform: translateY(-4px);
  }
  100% {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px);
  }
}
@keyframes hvr-bob {
  0% {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px);
  }
  50% {
    -webkit-transform: translateY(-4px);
    transform: translateY(-4px);
  }
  100% {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px);
  }
}
@-webkit-keyframes hvr-bob-float {
  100% {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px);
  }
}
@keyframes hvr-bob-float {
  100% {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px);
  }
}
.hvr-bob {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
}
.hvr-bob:hover, .hvr-bob:focus, .hvr-bob:active {
  -webkit-animation-name: hvr-bob-float, hvr-bob;
  animation-name: hvr-bob-float, hvr-bob;
  -webkit-animation-duration: .3s, 1.5s;
  animation-duration: .3s, 1.5s;
  -webkit-animation-delay: 0s, .3s;
  animation-delay: 0s, .3s;
  -webkit-animation-timing-function: ease-out, ease-in-out;
  animation-timing-function: ease-out, ease-in-out;
  -webkit-animation-iteration-count: 1, infinite;
  animation-iteration-count: 1, infinite;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
  -webkit-animation-direction: normal, alternate;
  animation-direction: normal, alternate;
}

/* Grow Rotate */
.hvr-grow-rotate {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: transform;
  transition-property: transform;
}
.hvr-grow-rotate:hover, .hvr-grow-rotate:focus, .hvr-grow-rotate:active {
  -webkit-transform: scale(1.1) rotate(4deg);
  transform: scale(1.1) rotate(4deg);
}

// hand wave
.wave {
  animation-name: wave-animation;  /* Refers to the name of your @keyframes element below */
  animation-duration: 2.5s;        /* Change to speed up or slow down */
  animation-iteration-count: infinite;  /* Never stop waving :) */
  transform-origin: 70% 70%;       /* Pivot around the bottom-left palm */
  display: inline-block;
}

@keyframes wave-animation {
    0% { transform: rotate( 0.0deg) }
   10% { transform: rotate(14.0deg) }  /* The following five values can be played with to make the waving more or less extreme */
   20% { transform: rotate(-8.0deg) }
   30% { transform: rotate(14.0deg) }
   40% { transform: rotate(-4.0deg) }
   50% { transform: rotate(10.0deg) }
   60% { transform: rotate( 0.0deg) }  /* Reset for the last half to pause */
  100% { transform: rotate( 0.0deg) }
}

//-----------------------------------------------------------

//Nav
.header-nav {
  display: flex;
  margin: 0 auto;
  justify-content: space-between;
  align-items: center;

  .name {
    font-family: "agustina-signaturesignature";
    font-size: 32px;
  }

  .nav {
    display: flex;

    a {
      text-decoration: none;
      color: #333333;
    }

    a:hover {
      color: black;
    }

    .nav-item {
      padding: 10px;
      font-size: 12px;
      opacity: 0.7;
    }
  }
}

//Hero
.sub-header {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 55px;
}

.header {
  font-size: 4rem;
  line-height: 20px;
  font-family: "Times New Roman", Times, serif;
  color: #000;
  margin: 26px 0;
}

mark {
	position: relative;
	display: inline-block;
	background: none;
	font-weight: 700;
	margin: 0;
	padding: 0;
	outline: none;
	text-decoration: none;
	white-space: nowrap;
}

.highlighter-svg {
	position: absolute;
	top: 10%;
	left: -10%;
	right: -10%;
	bottom: -10%;
	z-index: -1;
	overflow: hidden;
	margin: auto;
	padding: 0;
	width: 100%;
	height: 50%;
	fill: none;
	stroke-width: 54px;
	stroke-dasharray: 400;
	stroke-dashoffset: 400;
	transition: stroke-dashoffset 800ms ease-in-out;
	will-change: stroke-dashoffset;
	pointer-events: none;
}

.highlighter-svg:first-of-type {
	width: 105.5%;
	height: 50%;
	transform: skewX(-17deg);
}

.highlighter-svg:last-of-type {
	width: 104%;
	transform: skewX(-12deg);
}

mark:hover > .highlighter-svg {
	stroke-dashoffset: 0;
}

.header-description {
  margin: 30px auto;
}

.header-button {
  padding: 12px 20px;
  font: 400 1rem "Montserrat", sans-serif;
  text-transform: uppercase;
  background-color: #000;
  color: white;
  border: 2px solid black;

  &:hover {
    cursor: pointer;
  }
}


//Work
.project {
  display: flex;
  justify-content: space-between;
  margin: 30px auto;
  text-align: left;

  .project-category {
    text-transform: uppercase;
    letter-spacing: 0.2em;
    line-height: 150%;
    font-weight: 700;
    font-size: 10px;
    color: #b7b4b9;
  }

  .project-title {
    margin: 0 auto 15px;
  }

  .project-tags {
    display: flex;
    flex-wrap: wrap;
    max-width: 480px;
  }

  .project-tag {
    margin-right: 16px;
    margin-bottom: 16px;
    padding: 6px 10px;
    background-color: #e7f6f9;
    font-size: 13px;
    line-height: 16px;
    font-weight: 700;
  }

  .project-description {
    max-width: 480px;
  }

  .project-links {
    svg {
      height: 30px;
      width: 30px;
      margin-right: 15px;
    }
  }

  .project-image-link {
    display: block;
    margin-left: 50px;
    width: 45%;
  }

  img {
    max-width: 100%;
  }
}

//Skills
.skills {
  display: inline-flex;
  max-width: 740px;
  flex-wrap: wrap;
  justify-content: center;

  .skill-icon {
    padding: 12px;
    border-radius: 50%;
    margin: 0 10px 25px 10px;
    box-shadow: 0 15px 35px rgba(50, 50, 93, 0.1),
      0 5px 15px rgba(0, 0, 0, 0.07);
  }

  img {
    height: 50px;
    width: 60px;
  }

  .skill-details {
    margin-top: 30px;
    text-align: left;

    li {
      list-style-type: none;
      margin-bottom: 5px;
      font-size: 18px;
    }
  }
}

//About Me
.about {
  text-align: left;
}
.about-me {
  display: flex;

  img {
    height: auto;
    width: auto;
    max-width: 250px;
    max-height: 250px;
    margin-right: 90px;
  }

  .about-me-text {
    max-width: 600px;
    text-align: left;
    margin: 0;
    line-height: 37px;
  }
}

//Contact
.contact {
  background-color: #e7f6f9;
  padding: 60px 0;

  .contact-header {
    text-align: center;
    margin-top: 0;
    margin-bottom: 15px;
    line-height: normal;
  }
}
.footer {
  position: absolute;
  width: 100%;
  padding: 20px 0 20px 0;
  background-color: black;
  color: gray;
  display: flex;
  justify-content: space-between;

  .footer-copyright {
    margin-left: 50px;
    font-size: 12px;
  }

  .footer-links {
    margin-right: 50px;
    svg {
      fill: gray;
      height: 20px;
      width: 20px;

      path {
        fill: gray;
      }
    }

    a {
      color: gray;
      text-decoration: none;
      margin: 0 10px 0 10px;
    }
  }
}

//**Tablet**
@media (max-width: 1024px) {
  .section {
    margin: 8vh auto 0;
  }

  .section-header {
    font-size: 2rem;
  }

  .header {
    font-size: 3rem;
  }
  //Work
  .project {
    flex-direction: column;

    .project-description {
      max-width: 630px;
    }

    .project-details {
      margin: 0 auto 15px;
    }

    .project-image-link {
      width: auto;
      margin-left: 0;
    }
  }

  //about
  .about-me {
    flex-direction: column;
    align-items: center;

    img {
      margin: 0 auto 45px;
    }
  }
}

//**Phone**
@media (max-width: 720px) {
  .container {
    width: 85vw;
  }

  p {
    font-size: 14px;
    line-height: 22px;
  }

  .section {
    margin: 8vh auto 0;
  }

  .section-header {
    font-size: 1.25rem;
  }

  //Nav
  .header-nav {
    .name {
      font-size: 16px;
    }

    .nav {
      .nav-item {
        padding: 5px;
      }
    }
  }

  //Header
  .sub-header {
    font-size: 14px;
  }

  .header {
    font-size: 1.75rem;
    line-height: 10px;
  }

  .header-description {
    margin: 20px auto;
  }

  .header-button {
    font-size: 12px;
    padding: 10px;
  }

  //Work
  .project {
    .project-category {
      font-size: 9px;
    }

    .project-tag {
      margin-right: 10px;
      margin-bottom: 10px;
      font-size: 12px;
    }

    .project-details {
      max-width: 480px;
    }

    .project-links {
      svg {
        height: 25px;
        width: 25px;
        margin-right: 10px;
      }
    }
  }

  //Skills
  .skills {
    img {
      height: 30px;
      width: 30px;
    }
  }

  //about
  .about-me {
    img {
      margin-bottom: 10px;
    }
  }

  //contact
  .contact {
    padding: 35px 0;
  }

  .footer {
    padding: 15px 0 15px 0;
    .footer-copyright {
      margin-left: 10px;
      font-size: 10px;
    }

    .footer-links {
      margin-right: 10px;
      svg {
        height: 12px;
        width: 12px;
      }
    }
  }
}
